.upbtn {
    /* Structure */
    display: inline-block;
    zoom: 1;
    line-height: normal;
    white-space: nowrap;
    vertical-align: middle;
    text-align: center;
    cursor: pointer;
    user-select: none;
    box-sizing: border-box;
}

/* Firefox: Get rid of the inner focus border */
.upbtn::-moz-focus-inner {
    padding: 0;
    border: 0;
}

.upbtn {
    font-family: inherit;
    font-size: 100%;
    color: rgba(255, 255, 255, 1); /* rgba supported */
    background-color: #00A82D;
    text-decoration: none;
    border-radius: 25px;
    padding: 10px 30px;
    width: 100%;
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
}

.upbtn-hover, .upbtn:hover, .upbtn:focus {
    filter: alpha(opacity=90);
    background-image: linear-gradient(transparent, rgba(0, 0, 0, 0.05) 40%, rgba(0, 0, 0, 0.10));
}

.upbtn:focus {
    outline: 0;
}

.abbbtn__wrap {
    width: 100%;
    margin: 0 auto;
    padding: 20px;
}

.upbtn__text {
    flex: 1;
    font-weight: 500;
}

.upbtn__icon {
    margin: 5px;
}